import { useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Formik } from "formik";
import * as Yup from "yup";
import { InputBox, Buttons, SelectBox } from "../../../components/RenderFroms";
import { useFetchSingle } from "../../../contexts/useFetchSingle";
import { usePost } from "../../../contexts/usePost";
import { usePatch } from "../../../contexts/usePatch";
import Breadcrumbs from "../../../components/Breadcrumbs";
import ListTitle from "../../../components/ListTitle";
const resource = "system_smtp_servers";

const defaultValue = {
	systemSmtpServerName: "",
	systemSmtpServerHost: "",
	systemSmtpServerPort: "",
	systemSmtpServerEncryption: "",
	systemSmtpServerBounceReturnPathEmailId: "",
	systemSmtpServerUseAuthentication: ""
}

export default function AddSmtpServers() {
	const navigate = useNavigate();
	const { id } = useParams();

	const validationSchemaInfo = Yup.object().shape({
		systemSmtpServerName: Yup.string().required("Server Name is required"),
		systemSmtpServerHost: Yup.string().required("This is required"),
		systemSmtpServerPort: Yup.number().required("This is required"),
		systemSmtpServerEncryption: Yup.string().required("This is required"),
		systemSmtpServerUseAuthentication: Yup.string().required("This is required"),
		systemSmtpServerBounceReturnPathEmailId: Yup.string()
			.email("Invalid email address")
			.required("Email is Required"),

	});

	const { fetchSingle, data: users, loading: loadingData } = useFetchSingle();
	useEffect(() => {
		fetchSingle(resource, id)
	}, [id])

	const { create, data: respond, loading } = usePost();
	const { edit, data: respond1, loading: loading1 } = usePatch();
	const handleUpdate = (values: any) => {
		if (id) {
			edit(resource, values)
		} else {
			create(resource, values)
		}
	}

	useEffect(() => {
		if ((respond as any)?.status == 201) {
			navigate(-1)
		}
		if ((respond1 as any)?.status == 200) {
			navigate(-1)
		}
	}, [respond, respond1])

	const encryptOptions = [{ label: 'None', value: 'none' }, { label: 'SSL', value: 'ssl' }, { label: 'TLS', value: 'tls' }]
	const authOptions = [{ label: 'Yes, Authenticate Using Login Credentials', value: 'Yes' }, { label: 'No, Do Not Authenticate Using Login Credentials', value: 'No' }]

	if (loadingData) {
		return <>loading</>
	}

	return (
		<>
			<Breadcrumbs root="smtp-servers" pageName="Add SMTP Server" />
			<div className="main-content-body">
				<div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					<div className="row">
						<div className="card">

							<ListTitle ListName={`${id ? 'Edit' : 'Add'} SMTP Server (All fields are mandatory).`} />

							<div className="form-horizontal has-validation-callback">
								<Formik
									initialValues={users ? users : defaultValue}
									validationSchema={validationSchemaInfo}
									onSubmit={(values: any) => handleUpdate(values)}
								>
									{({ handleChange, handleBlur, handleSubmit, values, errors }) => (
										<>
											<div className="card-body">

												<InputBox
													required={true}
													name="systemSmtpServerName"
													lable="SMTP Server Name"
													placeholder=""
												/>

												<InputBox
													required={true}
													name="systemSmtpServerHost"
													lable="SMTP Server Host"
													placeholder=""
												/>

												<InputBox
													required={true}
													Type="number"
													name="systemSmtpServerPort"
													lable="SMTP Server Port No"
													placeholder=""
												/>

												<SelectBox
													required={true}
													options={encryptOptions}
													name="systemSmtpServerEncryption"
													lable="SMTP Server Encryption Method"
													placeholder="Select Type"
												/>

												<SelectBox
													required={true}
													options={authOptions}
													name="systemSmtpServerUseAuthentication"
													lable=">SMTP Server Use Authentication?"
													placeholder="Select"
												/>

												<InputBox
													required={true}
													name="systemSmtpServerBounceReturnPathEmailId"
													lable="SMTP Server Bounce Email ID"
													placeholder=""
												/>


												<div className="clear"></div>
											</div>
											<div className="card-action">
												<div className="form-group">
													<label className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">&nbsp;</label>
													<div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
														<Buttons loading={loading || loading1} className="btn-success" value="Save SMTP Server" onClick={() => handleSubmit()} />
														<Buttons className="btn-grey" value="Go Back to List" onClick={() => navigate(-1)} />
													</div>
												</div>
												<div className="clear"></div>
											</div>
										</>)}
								</Formik>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}


