import { NavLink } from "react-router-dom";
interface BreadcrumbProps {
  pageName: string;
  root: string;
}
const Breadcrumbs = ({ root, pageName }: BreadcrumbProps) => {
  return (
    <div className="main-content-header">
      <div className="card">
        <div className="card-body no-padding">
          <ol className="breadcrumb ltn no-radius">
            <li className="text-normal"><NavLink to="/dashboard">Main Dashboard</NavLink></li>
            {root && (<li className="text-normal"><NavLink to={`/${root}`}>{root}</NavLink></li>)}
            <li className="active text-lg">{pageName}</li>
          </ol>
        </div>
      </div>
    </div>
  );
};

export default Breadcrumbs;
